<template>
  <!-- 【单个报表页面】 -->
  <div class="dayVisit">
    <!-- 子菜单栏 -->
    <van-row class="sub-header">
      <!-- 左侧 -->
      <van-col class="text-left" span="18">&nbsp;</van-col>
      <!-- 右侧操作栏 -->
      <van-col class="text-right" span="6">
        <span class="btn-right" @click="handleRightBtn">{{ baseInfo.rightBtn }}</span>
      </van-col>
    </van-row>
    <!-- 报表标题、备注 -->
    <title-info :title="title" :remark="remark"></title-info>
    <!-- 报表内容 -->
    <el-table
    :data="tableData"
    :height="height"
    :empty-text="PROMPT.EMPTY_TEXT"
    header-cell-class-name="table-header"
    cell-class-name="table-cell"
    style="width: 100%"
    stripe>
      <el-table-column
      prop="album_title"
      label="字段1"
      min-width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="author"
      label="字段2"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="title"
      label="字段3"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="rank_change"
      label="字段4"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { NON_DYNAMIC_HEIGHT, PROMPT } from '@/utils'
import TitleInfo from '@/components/BaseTitleInfo'
import { testData } from '@/api'

export default {
  name: 'DayVisit',
  components: { TitleInfo },
  data () {
    return {
      PROMPT, // 提示信息合辑
      baseInfo: { // 界面基础设置
        rightBtn: '导航1_按钮' // 右上方按钮（单个）
      },
      title: '标题：单个报表模板', // 标题
      remark: ['(括号备注)', ''], // 备注
      tableData: [] // 表格数据
    }
  },
  computed: {
    height () { // 表格高度(18 - 一行备注的占高)
      return NON_DYNAMIC_HEIGHT - 18 * this.remark.length
    },
    freshTime () {
      return new Date().toLocaleString()
    }
  },
  beforeMount () {
    this.init()
  },
  methods: {
    init () {
      this.remark[1] = '更新时间： ' + this.freshTime // 要在挂载前进行该操作
      testData().then(response => {
        this.title = response.data.result[0].name
        this.remark[0] = response.data.result[0].color
        this.tableData = response.data.result[0].content
      })
    },
    handleRightBtn () { // 点击右上方按钮
      alert('sss')
    }
  }
}
</script>



